'use client';

import { useState, useEffect } from 'react';
import { useSearchParams, useRouter } from 'next/navigation';
import Link from 'next/link';
import {
  Search,
  Download,
  RefreshCw,
  Plus,
  HandMetal,
  Package,
  Tag,
  Truck,
  DollarSign,
  TrendingUp,
  Play,
  Eye,
  MoreVertical,
  Printer,
  MapPin,
  X,
  User,
  Calculator,
  BarChart3,
  Settings,
  ChevronLeft,
  ChevronRight,
} from 'lucide-react';
import {
  orders,
  getProductById,
  formatCurrencyPrecise,
  fulfillmentPipeline,
  dashboardStats,
} from '@/data/mockData';

type OrderStatus = 'to_pick' | 'to_pack' | 'ready_to_ship' | 'shipped' | 'delivered' | 'cancelled' | '';

export default function OrdersPage() {
  const searchParams = useSearchParams();
  const router = useRouter();
  const [searchQuery, setSearchQuery] = useState('');
  const [statusFilter, setStatusFilter] = useState<OrderStatus>('');
  const [channelFilter, setChannelFilter] = useState('');
  const [selectedOrderId, setSelectedOrderId] = useState<string | null>(null);

  // Read status from URL on mount
  useEffect(() => {
    const urlStatus = searchParams.get('status') as OrderStatus;
    if (urlStatus && ['to_pick', 'to_pack', 'ready_to_ship', 'shipped', 'delivered', 'cancelled'].includes(urlStatus)) {
      setStatusFilter(urlStatus);
    }
  }, [searchParams]);

  const filteredOrders = orders.filter((order) => {
    if (statusFilter && order.status !== statusFilter) return false;
    if (channelFilter && order.channel !== channelFilter) return false;
    if (searchQuery) {
      const query = searchQuery.toLowerCase();
      return (
        order.orderNumber.toLowerCase().includes(query) ||
        order.customer.name.toLowerCase().includes(query) ||
        order.customer.email?.toLowerCase().includes(query)
      );
    }
    return true;
  });

  const selectedOrder = selectedOrderId
    ? orders.find((o) => o.id === selectedOrderId)
    : null;

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'to_pick':
        return (
          <span className="inline-flex items-center gap-1 px-2.5 py-1 bg-amber-500/10 text-amber-400 text-xs font-medium rounded-full border border-amber-500/20">
            <HandMetal className="w-3 h-3" />
            To Pick
          </span>
        );
      case 'to_pack':
        return (
          <span className="inline-flex items-center gap-1 px-2.5 py-1 bg-blue-500/10 text-blue-400 text-xs font-medium rounded-full border border-blue-500/20">
            <Package className="w-3 h-3" />
            To Pack
          </span>
        );
      case 'ready_to_ship':
        return (
          <span className="inline-flex items-center gap-1 px-2.5 py-1 bg-purple-500/10 text-purple-400 text-xs font-medium rounded-full border border-purple-500/20">
            <Tag className="w-3 h-3" />
            Ready
          </span>
        );
      case 'shipped':
        return (
          <span className="inline-flex items-center gap-1 px-2.5 py-1 bg-emerald-500/10 text-emerald-400 text-xs font-medium rounded-full border border-emerald-500/20">
            <Truck className="w-3 h-3" />
            Shipped
          </span>
        );
      default:
        return (
          <span className="inline-flex items-center px-2.5 py-1 bg-slate-500/10 text-slate-400 text-xs font-medium rounded-full">
            {status}
          </span>
        );
    }
  };

  const getChannelBadge = (channel: string) => {
    switch (channel) {
      case 'shopify':
        return (
          <span className="inline-flex items-center gap-1.5 px-2.5 py-1 bg-green-500/10 text-green-400 text-xs font-medium rounded-full border border-green-500/20">
            <i className="fab fa-shopify"></i>
            Shopify
          </span>
        );
      case 'amazon_fbm':
        return (
          <span className="inline-flex items-center gap-1.5 px-2.5 py-1 bg-orange-500/10 text-orange-400 text-xs font-medium rounded-full border border-orange-500/20">
            <i className="fab fa-amazon"></i>
            Amazon FBM
          </span>
        );
      case 'amazon_fba':
        return (
          <span className="inline-flex items-center gap-1.5 px-2.5 py-1 bg-yellow-500/10 text-yellow-400 text-xs font-medium rounded-full border border-yellow-500/20">
            <i className="fab fa-amazon"></i>
            Amazon FBA
          </span>
        );
      case 'walmart':
        return (
          <span className="inline-flex items-center gap-1.5 px-2.5 py-1 bg-blue-500/10 text-blue-400 text-xs font-medium rounded-full border border-blue-500/20">
            <i className="fas fa-store"></i>
            Walmart
          </span>
        );
      case 'ebay':
        return (
          <span className="inline-flex items-center gap-1.5 px-2.5 py-1 bg-blue-600/10 text-blue-500 text-xs font-medium rounded-full border border-blue-600/20">
            <i className="fab fa-ebay"></i>
            eBay
          </span>
        );
      case 'etsy':
        return (
          <span className="inline-flex items-center gap-1.5 px-2.5 py-1 bg-orange-600/10 text-orange-500 text-xs font-medium rounded-full border border-orange-600/20">
            <i className="fab fa-etsy"></i>
            Etsy
          </span>
        );
      default:
        return (
          <span className="inline-flex items-center px-2.5 py-1 bg-slate-500/10 text-slate-400 text-xs font-medium rounded-full">
            {channel}
          </span>
        );
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-white">Orders</h1>
          <p className="text-sm text-slate-400">Manage and fulfill customer orders</p>
        </div>
        <div className="flex items-center gap-3">
          <button className="px-4 py-2 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-300 rounded-lg transition-colors flex items-center gap-2">
            <Download className="w-4 h-4" />
            Export
          </button>
          <button className="px-4 py-2 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-300 rounded-lg transition-colors flex items-center gap-2">
            <RefreshCw className="w-4 h-4" />
            Sync Orders
          </button>
          <Link href="/orders/new" className="px-4 py-2 bg-emerald-600 hover:bg-emerald-500 text-white rounded-lg transition-colors flex items-center gap-2">
            <Plus className="w-4 h-4" />
            Manual Order
          </Link>
        </div>
      </div>

      {/* Stats Row */}
      <div className="grid grid-cols-6 gap-4">
        <div
          className="bg-slate-800/50 backdrop-blur border border-amber-500/30 rounded-xl p-4 cursor-pointer hover:bg-amber-500/10 transition-colors"
          onClick={() => setStatusFilter('to_pick')}
        >
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-amber-500/20 flex items-center justify-center">
              <HandMetal className="w-5 h-5 text-amber-400" />
            </div>
            <div>
              <div className="text-2xl font-bold text-amber-400">{fulfillmentPipeline.toPick}</div>
              <div className="text-xs text-slate-400">To Pick</div>
            </div>
          </div>
        </div>
        <div
          className="bg-slate-800/50 backdrop-blur border border-blue-500/30 rounded-xl p-4 cursor-pointer hover:bg-blue-500/10 transition-colors"
          onClick={() => setStatusFilter('to_pack')}
        >
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-blue-500/20 flex items-center justify-center">
              <Package className="w-5 h-5 text-blue-400" />
            </div>
            <div>
              <div className="text-2xl font-bold text-blue-400">{fulfillmentPipeline.toPack}</div>
              <div className="text-xs text-slate-400">To Pack</div>
            </div>
          </div>
        </div>
        <div
          className="bg-slate-800/50 backdrop-blur border border-purple-500/30 rounded-xl p-4 cursor-pointer hover:bg-purple-500/10 transition-colors"
          onClick={() => setStatusFilter('ready_to_ship')}
        >
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-purple-500/20 flex items-center justify-center">
              <Tag className="w-5 h-5 text-purple-400" />
            </div>
            <div>
              <div className="text-2xl font-bold text-purple-400">{fulfillmentPipeline.readyToShip}</div>
              <div className="text-xs text-slate-400">Ready to Ship</div>
            </div>
          </div>
        </div>
        <div
          className="bg-slate-800/50 backdrop-blur border border-emerald-500/30 rounded-xl p-4 cursor-pointer hover:bg-emerald-500/10 transition-colors"
          onClick={() => setStatusFilter('shipped')}
        >
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-emerald-500/20 flex items-center justify-center">
              <Truck className="w-5 h-5 text-emerald-400" />
            </div>
            <div>
              <div className="text-2xl font-bold text-emerald-400">{fulfillmentPipeline.shipped}</div>
              <div className="text-xs text-slate-400">Shipped Today</div>
            </div>
          </div>
        </div>
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-slate-700/50 flex items-center justify-center">
              <DollarSign className="w-5 h-5 text-slate-400" />
            </div>
            <div>
              <div className="text-2xl font-bold text-white">{formatCurrencyPrecise(dashboardStats.revenue)}</div>
              <div className="text-xs text-slate-400">Today&apos;s Revenue</div>
            </div>
          </div>
        </div>
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-lg bg-emerald-500/20 flex items-center justify-center">
              <TrendingUp className="w-5 h-5 text-emerald-400" />
            </div>
            <div>
              <div className="text-2xl font-bold text-emerald-400">{formatCurrencyPrecise(dashboardStats.grossProfit)}</div>
              <div className="text-xs text-slate-400">Today&apos;s Profit</div>
            </div>
          </div>
        </div>
      </div>

      {/* Filters & Search */}
      <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
        <div className="flex items-center gap-4">
          {/* Search */}
          <div className="flex-1 relative">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-slate-400" />
            <input
              type="text"
              placeholder="Search by order #, customer name, email..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full bg-slate-800/50 border border-slate-700 rounded-lg pl-10 pr-4 py-2.5 text-white placeholder-slate-400 focus:outline-none focus:border-emerald-500/50 focus:ring-1 focus:ring-emerald-500/50"
            />
          </div>

          {/* Status Filter */}
          <select
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value as OrderStatus)}
            className="bg-slate-800/50 border border-slate-700 rounded-lg px-4 py-2.5 text-slate-300 focus:outline-none focus:border-emerald-500/50"
          >
            <option value="">All Statuses</option>
            <option value="to_pick">To Pick</option>
            <option value="to_pack">To Pack</option>
            <option value="ready_to_ship">Ready to Ship</option>
            <option value="shipped">Shipped</option>
            <option value="delivered">Delivered</option>
            <option value="cancelled">Cancelled</option>
          </select>

          {/* Channel Filter */}
          <select
            value={channelFilter}
            onChange={(e) => setChannelFilter(e.target.value)}
            className="bg-slate-800/50 border border-slate-700 rounded-lg px-4 py-2.5 text-slate-300 focus:outline-none focus:border-emerald-500/50"
          >
            <option value="">All Channels</option>
            <option value="shopify">Shopify</option>
            <option value="amazon_fbm">Amazon FBM</option>
            <option value="amazon_fba">Amazon FBA</option>
            <option value="manual">Manual</option>
          </select>

          {/* Date Filter */}
          <select className="bg-slate-800/50 border border-slate-700 rounded-lg px-4 py-2.5 text-slate-300 focus:outline-none focus:border-emerald-500/50">
            <option>Today</option>
            <option>Yesterday</option>
            <option>Last 7 days</option>
            <option>Last 30 days</option>
            <option>This month</option>
            <option>Custom range</option>
          </select>

          {/* Bulk Actions */}
          <button className="px-4 py-2.5 bg-slate-800/50 border border-slate-700 rounded-lg text-slate-300 hover:bg-slate-700 transition-colors flex items-center gap-2">
            Bulk Actions
            <ChevronRight className="w-4 h-4 rotate-90" />
          </button>
        </div>
      </div>

      {/* Orders Table */}
      <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-slate-800/50 border-b border-slate-700/50">
              <tr className="text-left text-xs text-slate-400 uppercase tracking-wider">
                <th className="px-4 py-3 font-medium">
                  <input type="checkbox" className="rounded bg-slate-700 border-slate-600" />
                </th>
                <th className="px-4 py-3 font-medium">Order</th>
                <th className="px-4 py-3 font-medium">Date</th>
                <th className="px-4 py-3 font-medium">Customer</th>
                <th className="px-4 py-3 font-medium">Channel</th>
                <th className="px-4 py-3 font-medium">Items</th>
                <th className="px-4 py-3 font-medium text-right">Total</th>
                <th className="px-4 py-3 font-medium text-right">COGS</th>
                <th className="px-4 py-3 font-medium text-right">Profit</th>
                <th className="px-4 py-3 font-medium text-center">Margin</th>
                <th className="px-4 py-3 font-medium text-center">Status</th>
                <th className="px-4 py-3 font-medium text-center">Actions</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-slate-700/50">
              {filteredOrders.map((order) => {
                const itemCount = order.items.reduce((sum, item) => sum + item.quantity, 0);
                const skuCount = order.items.length;
                const createdAt = new Date(order.createdAt);

                return (
                  <tr
                    key={order.id}
                    className="hover:bg-slate-700/30 transition-colors cursor-pointer"
                    onClick={() => router.push(`/orders/${order.id}`)}
                  >
                    <td className="px-4 py-4" onClick={(e) => e.stopPropagation()}>
                      <input type="checkbox" className="rounded bg-slate-700 border-slate-600" />
                    </td>
                    <td className="px-4 py-4">
                      <div className="font-mono text-sm font-medium text-white">#{order.orderNumber}</div>
                      <div className="text-xs text-slate-500">{order.externalId}</div>
                    </td>
                    <td className="px-4 py-4">
                      <div className="text-sm text-white">
                        {createdAt.toLocaleDateString('en-US', { month: 'short', day: 'numeric', year: 'numeric' })}
                      </div>
                      <div className="text-xs text-slate-500">
                        {createdAt.toLocaleTimeString('en-US', { hour: 'numeric', minute: '2-digit' })}
                      </div>
                    </td>
                    <td className="px-4 py-4">
                      <div className="text-sm font-medium text-white">{order.customer.name}</div>
                      <div className="text-xs text-slate-400">{order.customer.email}</div>
                      <div className="text-xs text-slate-500">
                        {order.shippingAddress.city}, {order.shippingAddress.state} {order.shippingAddress.postalCode}
                      </div>
                    </td>
                    <td className="px-4 py-4">{getChannelBadge(order.channel)}</td>
                    <td className="px-4 py-4">
                      <div className="text-sm text-white">{itemCount} items</div>
                      <div className="text-xs text-slate-500">{skuCount} SKUs</div>
                    </td>
                    <td className="px-4 py-4 text-right">
                      <div className="text-sm font-medium text-white">{formatCurrencyPrecise(order.total)}</div>
                      <div className="text-xs text-slate-500">
                        {order.shipping > 0 ? `+ ${formatCurrencyPrecise(order.shipping)} ship` : 'Free ship'}
                      </div>
                    </td>
                    <td className="px-4 py-4 text-right">
                      <div className="text-sm text-slate-300">{formatCurrencyPrecise(order.cogs)}</div>
                    </td>
                    <td className="px-4 py-4 text-right">
                      <div className="text-sm font-medium text-emerald-400">{formatCurrencyPrecise(order.profit)}</div>
                    </td>
                    <td className="px-4 py-4 text-center">
                      <span className="inline-flex items-center px-2 py-1 bg-emerald-500/10 text-emerald-400 text-xs font-medium rounded-full">
                        {order.marginPercent.toFixed(1)}%
                      </span>
                    </td>
                    <td className="px-4 py-4 text-center">
                      {getStatusBadge(order.status)}
                      {order.status === 'shipped' && order.shipments[0]?.trackingNumber && (
                        <div className="text-xs text-slate-500 mt-1">
                          {order.shipments[0].trackingNumber.slice(0, 12)}...
                        </div>
                      )}
                    </td>
                    <td className="px-4 py-4" onClick={(e) => e.stopPropagation()}>
                      <div className="flex items-center justify-center gap-1">
                        {order.status === 'to_pick' && (
                          <button className="p-2 text-slate-400 hover:text-emerald-400 hover:bg-emerald-500/10 rounded-lg transition-colors" title="Start Pick">
                            <Play className="w-4 h-4" />
                          </button>
                        )}
                        {order.status === 'to_pack' && (
                          <button className="p-2 text-slate-400 hover:text-blue-400 hover:bg-blue-500/10 rounded-lg transition-colors" title="Start Pack">
                            <Package className="w-4 h-4" />
                          </button>
                        )}
                        {order.status === 'ready_to_ship' && (
                          <button className="p-2 text-slate-400 hover:text-purple-400 hover:bg-purple-500/10 rounded-lg transition-colors" title="Print Label">
                            <Printer className="w-4 h-4" />
                          </button>
                        )}
                        {order.status === 'shipped' && (
                          <button className="p-2 text-slate-400 hover:text-white hover:bg-slate-700 rounded-lg transition-colors" title="Track">
                            <MapPin className="w-4 h-4" />
                          </button>
                        )}
                        <button className="p-2 text-slate-400 hover:text-white hover:bg-slate-700 rounded-lg transition-colors" title="View">
                          <Eye className="w-4 h-4" />
                        </button>
                        <button className="p-2 text-slate-400 hover:text-white hover:bg-slate-700 rounded-lg transition-colors" title="More">
                          <MoreVertical className="w-4 h-4" />
                        </button>
                      </div>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        <div className="px-5 py-4 border-t border-slate-700/50 flex items-center justify-between">
          <div className="text-sm text-slate-400">
            Showing <span className="text-white">1-{filteredOrders.length}</span> of <span className="text-white">{orders.length}</span> orders
          </div>
          <div className="flex items-center gap-2">
            <button className="px-3 py-1.5 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-400 rounded-lg transition-colors disabled:opacity-50" disabled>
              <ChevronLeft className="w-4 h-4" />
            </button>
            <button className="px-3 py-1.5 bg-emerald-500/20 text-emerald-400 border border-emerald-500/30 rounded-lg">1</button>
            <button className="px-3 py-1.5 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-400 rounded-lg transition-colors">
              <ChevronRight className="w-4 h-4" />
            </button>
          </div>
        </div>
      </div>

      {/* Order Detail Slide-over */}
      {selectedOrder && (
        <div className="fixed inset-0 z-50">
          <div className="absolute inset-0 bg-black/50 backdrop-blur-sm" onClick={() => setSelectedOrderId(null)} />
          <div className="absolute right-0 top-0 h-full w-full max-w-2xl bg-slate-900 border-l border-slate-700 shadow-2xl overflow-auto">
            {/* Panel Header */}
            <div className="sticky top-0 bg-slate-900 border-b border-slate-700 px-6 py-4 flex items-center justify-between">
              <div>
                <h2 className="text-xl font-bold text-white">Order #{selectedOrder.orderNumber}</h2>
                <p className="text-sm text-slate-400">
                  {new Date(selectedOrder.createdAt).toLocaleDateString('en-US', {
                    month: 'long',
                    day: 'numeric',
                    year: 'numeric',
                  })} at {new Date(selectedOrder.createdAt).toLocaleTimeString('en-US', {
                    hour: 'numeric',
                    minute: '2-digit',
                  })}
                </p>
              </div>
              <button onClick={() => setSelectedOrderId(null)} className="p-2 text-slate-400 hover:text-white hover:bg-slate-800 rounded-lg">
                <X className="w-6 h-6" />
              </button>
            </div>

            {/* Panel Content */}
            <div className="p-6 space-y-6">
              {/* Status & Channel */}
              <div className="flex items-center gap-3">
                {getStatusBadge(selectedOrder.status)}
                {getChannelBadge(selectedOrder.channel)}
              </div>

              {/* Customer Info */}
              <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-5">
                <h3 className="font-semibold text-white mb-4 flex items-center gap-2">
                  <User className="w-4 h-4 text-slate-400" />
                  Customer
                </h3>
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <div className="text-sm text-slate-400 mb-1">Name</div>
                    <div className="text-white">{selectedOrder.customer.name}</div>
                  </div>
                  <div>
                    <div className="text-sm text-slate-400 mb-1">Email</div>
                    <div className="text-white">{selectedOrder.customer.email}</div>
                  </div>
                  <div className="col-span-2">
                    <div className="text-sm text-slate-400 mb-1">Shipping Address</div>
                    <div className="text-white">
                      {selectedOrder.shippingAddress.line1}<br />
                      {selectedOrder.shippingAddress.city}, {selectedOrder.shippingAddress.state} {selectedOrder.shippingAddress.postalCode}<br />
                      {selectedOrder.shippingAddress.country === 'US' ? 'United States' : selectedOrder.shippingAddress.country}
                    </div>
                  </div>
                </div>
              </div>

              {/* Order Items */}
              <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl overflow-hidden">
                <div className="px-5 py-4 border-b border-slate-700/50">
                  <h3 className="font-semibold text-white flex items-center gap-2">
                    <Package className="w-4 h-4 text-slate-400" />
                    Items ({selectedOrder.items.reduce((sum, item) => sum + item.quantity, 0)})
                  </h3>
                </div>
                <div className="divide-y divide-slate-700/50">
                  {selectedOrder.items.map((item) => {
                    const product = item.productId ? getProductById(item.productId) : null;
                    return (
                      <div key={item.id} className="px-5 py-4 flex items-center gap-4">
                        <div className="w-12 h-12 bg-emerald-500/20 rounded-lg flex items-center justify-center">
                          <Package className="w-5 h-5 text-emerald-400" />
                        </div>
                        <div className="flex-1">
                          <div className="font-medium text-white">{item.name}</div>
                          <div className="text-xs text-slate-400">
                            SKU: {item.sku}
                            {product && ` - Bin: ${product.id}`}
                          </div>
                        </div>
                        <div className="text-right">
                          <div className="text-white">x {item.quantity}</div>
                          <div className="text-sm text-slate-400">{formatCurrencyPrecise(item.price * item.quantity)}</div>
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>

              {/* Order Totals */}
              <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-5">
                <h3 className="font-semibold text-white mb-4 flex items-center gap-2">
                  <Calculator className="w-4 h-4 text-slate-400" />
                  Order Summary
                </h3>
                <div className="space-y-2">
                  <div className="flex justify-between text-sm">
                    <span className="text-slate-400">Subtotal</span>
                    <span className="text-white">{formatCurrencyPrecise(selectedOrder.subtotal)}</span>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span className="text-slate-400">Shipping</span>
                    <span className="text-white">{formatCurrencyPrecise(selectedOrder.shipping)}</span>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span className="text-slate-400">Tax</span>
                    <span className="text-white">{formatCurrencyPrecise(selectedOrder.tax)}</span>
                  </div>
                  <div className="border-t border-slate-700 pt-2 mt-2">
                    <div className="flex justify-between font-medium">
                      <span className="text-white">Total</span>
                      <span className="text-white">{formatCurrencyPrecise(selectedOrder.total)}</span>
                    </div>
                  </div>
                </div>
              </div>

              {/* Profitability */}
              <div className="bg-gradient-to-r from-emerald-500/10 to-blue-500/10 border border-emerald-500/20 rounded-xl p-5">
                <h3 className="font-semibold text-white mb-4 flex items-center gap-2">
                  <BarChart3 className="w-4 h-4 text-emerald-400" />
                  Profitability
                </h3>
                <div className="grid grid-cols-3 gap-4 text-center">
                  <div>
                    <div className="text-2xl font-bold text-white">{formatCurrencyPrecise(selectedOrder.total)}</div>
                    <div className="text-xs text-slate-400">Revenue</div>
                  </div>
                  <div>
                    <div className="text-2xl font-bold text-slate-300">{formatCurrencyPrecise(selectedOrder.cogs)}</div>
                    <div className="text-xs text-slate-400">COGS</div>
                  </div>
                  <div>
                    <div className="text-2xl font-bold text-emerald-400">{formatCurrencyPrecise(selectedOrder.profit)}</div>
                    <div className="text-xs text-slate-400">Profit ({selectedOrder.marginPercent.toFixed(1)}%)</div>
                  </div>
                </div>
              </div>

              {/* Fulfillment Rules */}
              <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-5">
                <h3 className="font-semibold text-white mb-4 flex items-center gap-2">
                  <Settings className="w-4 h-4 text-slate-400" />
                  Fulfillment Rules
                </h3>
                <div className="space-y-3">
                  <div className="flex items-center gap-3 p-3 bg-slate-800/50 rounded-lg">
                    <div className="w-8 h-8 rounded bg-amber-500/20 flex items-center justify-center">
                      <Package className="w-4 h-4 text-amber-400" />
                    </div>
                    <div className="flex-1">
                      <div className="text-sm font-medium text-white">
                        {selectedOrder.suggestedBoxId ? 'Suggested Box' : 'No Box Suggested'}
                      </div>
                      <div className="text-xs text-slate-400">
                        {selectedOrder.items.reduce((sum, item) => sum + item.quantity, 0)} items
                      </div>
                    </div>
                    <div className="text-emerald-400">
                      <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                      </svg>
                    </div>
                  </div>
                  <div className="flex items-center gap-3 p-3 bg-slate-800/50 rounded-lg">
                    <div className="w-8 h-8 rounded bg-blue-500/20 flex items-center justify-center">
                      <Tag className="w-4 h-4 text-blue-400" />
                    </div>
                    <div className="flex-1">
                      <div className="text-sm font-medium text-white">{selectedOrder.gripperStickersNeeded} Gripper Stickers</div>
                      <div className="text-xs text-slate-400">1 per bottle</div>
                    </div>
                    <div className="text-emerald-400">
                      <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                      </svg>
                    </div>
                  </div>
                </div>
              </div>

              {/* Actions */}
              <div className="flex gap-3">
                {selectedOrder.status === 'to_pick' && (
                  <button className="flex-1 px-4 py-3 bg-emerald-600 hover:bg-emerald-500 text-white rounded-lg transition-colors flex items-center justify-center gap-2 font-medium">
                    <Play className="w-4 h-4" />
                    Start Picking
                  </button>
                )}
                {selectedOrder.status === 'to_pack' && (
                  <button className="flex-1 px-4 py-3 bg-blue-600 hover:bg-blue-500 text-white rounded-lg transition-colors flex items-center justify-center gap-2 font-medium">
                    <Package className="w-4 h-4" />
                    Start Packing
                  </button>
                )}
                {selectedOrder.status === 'ready_to_ship' && (
                  <button className="flex-1 px-4 py-3 bg-purple-600 hover:bg-purple-500 text-white rounded-lg transition-colors flex items-center justify-center gap-2 font-medium">
                    <Printer className="w-4 h-4" />
                    Print Label
                  </button>
                )}
                <button className="px-4 py-3 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-300 rounded-lg transition-colors">
                  <Printer className="w-4 h-4" />
                </button>
                <button className="px-4 py-3 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-300 rounded-lg transition-colors">
                  <MoreVertical className="w-4 h-4" />
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
